'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group';
import { Label } from '@/components/ui/label';
import { Progress } from '@/components/ui/progress';

interface SurveyQuestion {
  id: string;
  question: string;
  options: string[];
}

interface PersonalitySurveyProps {
  onComplete: (responses: { question: string; answer: string }[]) => void;
  isLoading?: boolean;
}

const surveyQuestions: SurveyQuestion[] = [
  {
    id: 'social_preference',
    question: '¿Cómo prefieres pasar tu tiempo libre?',
    options: [
      'En casa leyendo o viendo películas',
      'Saliendo con un grupo pequeño de amigos cercanos',
      'En fiestas o eventos sociales grandes',
      'Explorando lugares nuevos solo o con otros',
      'Haciendo actividades al aire libre'
    ]
  },
  {
    id: 'new_people',
    question: '¿Cómo te sientes al conocer gente nueva?',
    options: [
      'Me pone muy nervioso, prefiero evitarlo',
      'Me siento un poco incómodo al principio',
      'Me emociona conocer personas nuevas',
      'Me encanta, siempre busco hacer nuevos amigos',
      'Depende del contexto y la situación'
    ]
  },
  {
    id: 'risk_taking',
    question: '¿Qué tan dispuesto estás a probar actividades nuevas o arriesgadas?',
    options: [
      'Prefiero actividades familiares y seguras',
      'Probaría algo nuevo si otros me acompañan',
      'Me gusta probar cosas nuevas ocasionalmente',
      'Siempre busco nuevas aventuras y desafíos',
      'Me encantan las actividades extremas y emocionantes'
    ]
  },
  {
    id: 'group_dynamics',
    question: '¿Cómo te comportas en grupos?',
    options: [
      'Prefiero escuchar y observar',
      'Participo cuando me siento cómodo',
      'Me gusta contribuir a las conversaciones',
      'Suelo ser el centro de atención',
      'Me gusta liderar y organizar actividades'
    ]
  },
  {
    id: 'stress_response',
    question: '¿Cómo manejas situaciones estresantes o desafiantes?',
    options: [
      'Me retiro y busco tranquilidad',
      'Busco apoyo en amigos cercanos',
      'Trato de mantener la calma y pensar soluciones',
      'Me motivo con el desafío',
      'Disfruto la adrenalina del momento'
    ]
  },
  {
    id: 'weekend_ideal',
    question: '¿Cuál sería tu fin de semana ideal?',
    options: [
      'Relajándome en casa con mis hobbies',
      'Una cena tranquila con amigos cercanos',
      'Una fiesta o evento social animado',
      'Una aventura o viaje espontáneo',
      'Actividades deportivas o al aire libre'
    ]
  },
  {
    id: 'decision_making',
    question: '¿Cómo tomas decisiones importantes?',
    options: [
      'Analizo mucho y busco la opción más segura',
      'Consulto con personas de confianza',
      'Considero pros y contras cuidadosamente',
      'Confío en mi intuición',
      'Tomo decisiones rápidas y me adapto'
    ]
  },
  {
    id: 'energy_source',
    question: '¿De dónde obtienes tu energía?',
    options: [
      'Del tiempo a solas y la reflexión',
      'De conversaciones profundas con pocos amigos',
      'De la interacción social y las actividades grupales',
      'De nuevas experiencias y aventuras',
      'De desafíos físicos y mentales'
    ]
  }
];

export default function PersonalitySurvey({ onComplete, isLoading = false }: PersonalitySurveyProps) {
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [responses, setResponses] = useState<Record<string, string>>({});

  const progress = ((currentQuestion + 1) / surveyQuestions.length) * 100;
  const isLastQuestion = currentQuestion === surveyQuestions.length - 1;
  const canProceed = responses[surveyQuestions[currentQuestion].id];

  const handleAnswer = (answer: string) => {
    setResponses(prev => ({
      ...prev,
      [surveyQuestions[currentQuestion].id]: answer
    }));
  };

  const handleNext = () => {
    if (isLastQuestion) {
      // Convert responses to the expected format
      const formattedResponses = surveyQuestions.map(q => ({
        question: q.question,
        answer: responses[q.id]
      }));
      onComplete(formattedResponses);
    } else {
      setCurrentQuestion(prev => prev + 1);
    }
  };

  const handlePrevious = () => {
    if (currentQuestion > 0) {
      setCurrentQuestion(prev => prev - 1);
    }
  };

  const question = surveyQuestions[currentQuestion];

  return (
    <Card className="w-full max-w-2xl mx-auto">
      <CardHeader>
        <div className="space-y-2">
          <div className="flex justify-between items-center">
            <CardTitle>Análisis de Personalidad</CardTitle>
            <span className="text-sm text-muted-foreground">
              {currentQuestion + 1} de {surveyQuestions.length}
            </span>
          </div>
          <Progress value={progress} className="w-full" />
        </div>
        <CardDescription>
          Responde estas preguntas para que podamos sugerirte las mejores actividades
        </CardDescription>
      </CardHeader>
      
      <CardContent className="space-y-6">
        <div>
          <h3 className="text-lg font-medium mb-4">{question.question}</h3>
          
          <RadioGroup
            value={responses[question.id] || ''}
            onValueChange={handleAnswer}
            className="space-y-3"
          >
            {question.options.map((option, index) => (
              <div key={index} className="flex items-center space-x-2">
                <RadioGroupItem value={option} id={`option-${index}`} />
                <Label 
                  htmlFor={`option-${index}`} 
                  className="text-sm leading-relaxed cursor-pointer"
                >
                  {option}
                </Label>
              </div>
            ))}
          </RadioGroup>
        </div>

        <div className="flex justify-between pt-4">
          <Button
            variant="outline"
            onClick={handlePrevious}
            disabled={currentQuestion === 0 || isLoading}
          >
            Anterior
          </Button>
          
          <Button
            onClick={handleNext}
            disabled={!canProceed || isLoading}
          >
            {isLoading ? 'Analizando...' : isLastQuestion ? 'Finalizar' : 'Siguiente'}
          </Button>
        </div>
      </CardContent>
    </Card>
  );
}
