'use client';

import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Checkbox } from '@/components/ui/checkbox';
import { Alert, AlertDescription } from '@/components/ui/alert';
import PersonalitySurvey from './PersonalitySurvey';
import { PersonalityResult } from '@/lib/personalityService';

const registrationSchema = z.object({
  name: z.string().min(2, 'El nombre debe tener al menos 2 caracteres'),
  email: z.string().email('Email inválido'),
  age: z.number().min(13, 'Debes tener al menos 13 años').max(100, 'Edad inválida'),
  location: z.string().min(2, 'La ubicación es requerida'),
  interests: z.string().min(10, 'Describe tus intereses (mínimo 10 caracteres)'),
  isMinor: z.boolean(),
  parentalConsent: z.boolean().optional(),
  termsAccepted: z.boolean().refine(val => val === true, 'Debes aceptar los términos y condiciones')
});

type RegistrationFormData = z.infer<typeof registrationSchema>;

interface RegistrationFormProps {
  onComplete?: (userData: RegistrationFormData & { personalityResult: PersonalityResult }) => void;
}

export default function RegistrationForm({ onComplete }: RegistrationFormProps) {
  const [step, setStep] = useState<'basic' | 'survey' | 'result'>('basic');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [personalityResult, setPersonalityResult] = useState<PersonalityResult | null>(null);
  const [formData, setFormData] = useState<RegistrationFormData | null>(null);

  const {
    register,
    handleSubmit,
    watch,
    setValue,
    formState: { errors }
  } = useForm<RegistrationFormData>({
    resolver: zodResolver(registrationSchema)
  });

  const age = watch('age');
  const isMinor = age && age < 18;
  const parentalConsent = watch('parentalConsent');

  // Update isMinor field when age changes
  if (isMinor !== watch('isMinor')) {
    setValue('isMinor', isMinor || false);
  }

  const onBasicFormSubmit = (data: RegistrationFormData) => {
    if (isMinor && !parentalConsent) {
      setError('Se requiere consentimiento parental para menores de edad');
      return;
    }
    
    setFormData(data);
    setError(null);
    setStep('survey');
  };

  const handleSurveyComplete = async (responses: { question: string; answer: string }[]) => {
    if (!formData) return;
    
    setIsLoading(true);
    setError(null);

    try {
      const surveyData = {
        responses,
        userInfo: {
          age: formData.age,
          location: formData.location,
          interests: formData.interests.split(',').map(i => i.trim())
        }
      };

      const response = await fetch('/api/personality', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(surveyData)
      });

      const result = await response.json();

      if (!result.success) {
        throw new Error(result.error || 'Error al analizar personalidad');
      }

      setPersonalityResult(result.data);
      setStep('result');

      // Call onComplete if provided
      if (onComplete) {
        onComplete({
          ...formData,
          personalityResult: result.data
        });
      }

    } catch (err) {
      setError(err instanceof Error ? err.message : 'Error inesperado');
    } finally {
      setIsLoading(false);
    }
  };

  const handleStartOver = () => {
    setStep('basic');
    setFormData(null);
    setPersonalityResult(null);
    setError(null);
  };

  if (step === 'survey') {
    return (
      <div className="space-y-6">
        {error && (
          <Alert variant="destructive">
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}
        <PersonalitySurvey 
          onComplete={handleSurveyComplete}
          isLoading={isLoading}
        />
        <div className="text-center">
          <Button variant="outline" onClick={() => setStep('basic')}>
            Volver al formulario
          </Button>
        </div>
      </div>
    );
  }

  if (step === 'result' && personalityResult) {
    return (
      <Card className="w-full max-w-2xl mx-auto">
        <CardHeader>
          <CardTitle>¡Tu Perfil de Personalidad!</CardTitle>
          <CardDescription>
            Basado en tus respuestas, hemos determinado tu tipo de personalidad
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          <div className="text-center p-6 bg-muted rounded-lg">
            <h3 className="text-2xl font-bold capitalize mb-2">
              {personalityResult.type}
            </h3>
            <p className="text-muted-foreground">
              {personalityResult.description}
            </p>
            <div className="mt-4">
              <span className="text-sm text-muted-foreground">
                Confianza del análisis: {Math.round(personalityResult.confidence * 100)}%
              </span>
            </div>
          </div>

          <div>
            <h4 className="font-semibold mb-3">Actividades recomendadas para ti:</h4>
            <div className="grid gap-2">
              {personalityResult.suggestedActivities.map((activity, index) => (
                <div key={index} className="p-3 bg-card border rounded-lg">
                  <span className="text-sm">{activity}</span>
                </div>
              ))}
            </div>
          </div>

          <div className="flex gap-4 pt-4">
            <Button onClick={() => window.location.href = '/activities'} className="flex-1">
              Ver Actividades Disponibles
            </Button>
            <Button variant="outline" onClick={handleStartOver}>
              Hacer Test Nuevamente
            </Button>
          </div>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className="w-full max-w-2xl mx-auto">
      <CardHeader>
        <CardTitle>Crear Cuenta</CardTitle>
        <CardDescription>
          Completa tu información para comenzar a conectar con personas increíbles
        </CardDescription>
      </CardHeader>
      <CardContent>
        {error && (
          <Alert variant="destructive" className="mb-6">
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}

        <form onSubmit={handleSubmit(onBasicFormSubmit)} className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="name">Nombre completo</Label>
            <Input
              id="name"
              {...register('name')}
              placeholder="Tu nombre completo"
            />
            {errors.name && (
              <p className="text-sm text-destructive">{errors.name.message}</p>
            )}
          </div>

          <div className="space-y-2">
            <Label htmlFor="email">Email</Label>
            <Input
              id="email"
              type="email"
              {...register('email')}
              placeholder="tu@email.com"
            />
            {errors.email && (
              <p className="text-sm text-destructive">{errors.email.message}</p>
            )}
          </div>

          <div className="space-y-2">
            <Label htmlFor="age">Edad</Label>
            <Input
              id="age"
              type="number"
              {...register('age', { valueAsNumber: true })}
              placeholder="Tu edad"
            />
            {errors.age && (
              <p className="text-sm text-destructive">{errors.age.message}</p>
            )}
          </div>

          <div className="space-y-2">
            <Label htmlFor="location">Ubicación</Label>
            <Input
              id="location"
              {...register('location')}
              placeholder="Ciudad, País"
            />
            {errors.location && (
              <p className="text-sm text-destructive">{errors.location.message}</p>
            )}
          </div>

          <div className="space-y-2">
            <Label htmlFor="interests">Intereses</Label>
            <Textarea
              id="interests"
              {...register('interests')}
              placeholder="Describe tus intereses, hobbies y actividades favoritas..."
              rows={3}
            />
            {errors.interests && (
              <p className="text-sm text-destructive">{errors.interests.message}</p>
            )}
          </div>

          {isMinor && (
            <div className="p-4 bg-muted rounded-lg space-y-3">
              <p className="text-sm font-medium">Información para menores de edad:</p>
              <p className="text-sm text-muted-foreground">
                Como eres menor de 18 años, se aplicarán restricciones especiales:
              </p>
              <ul className="text-sm text-muted-foreground list-disc list-inside space-y-1">
                <li>Actividades limitadas a 30km de tu ubicación</li>
                <li>Solo podrás unirte a actividades con personas menores de 17 años</li>
                <li>Todas las actividades tendrán supervisión profesional</li>
              </ul>
              
              <div className="flex items-center space-x-2">
                <Checkbox
                  id="parentalConsent"
                  {...register('parentalConsent')}
                />
                <Label htmlFor="parentalConsent" className="text-sm">
                  Confirmo que tengo consentimiento parental para usar esta aplicación
                </Label>
              </div>
            </div>
          )}

          <div className="flex items-center space-x-2">
            <Checkbox
              id="termsAccepted"
              {...register('termsAccepted')}
            />
            <Label htmlFor="termsAccepted" className="text-sm">
              Acepto los términos y condiciones y la política de privacidad
            </Label>
          </div>
          {errors.termsAccepted && (
            <p className="text-sm text-destructive">{errors.termsAccepted.message}</p>
          )}

          <Button type="submit" className="w-full">
            Continuar al Análisis de Personalidad
          </Button>
        </form>
      </CardContent>
    </Card>
  );
}
